  }

  .se1_7 img {
    padding: 7px;
    background-color: #78c9ce;
    width: 40px;
    height: 40px;
    border-radius: 20px;
    margin-top: 0px;
  }
  .se1_9 {
    margin-left: 10px;
  }
  .se1_9 h3 {
    margin-top: 5px;
  }
  .se1_10 {
    display: flex;
    margin-top: 0px;
  }

  .se1_11 {
    margin-top: 7px;
    font-size: 20px;
  }

  .se1_12 {
    margin-left: 5px;
    width: 20px;
    height: 20px;
    border-radius: 10px;
    text-align: center;
    color: white;
    background-color: #ce78c5;
    padding: 2px;
  }
  .switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: 0.4s;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: 0.4s;
  }

  input:checked + .slider {
    background-color: #2196f3;
  }

  input:focus + .slider {
    box-shadow: 0 0 1px #2196f3;
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    transform: translateX(26px);
  }

  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }

  .slider.round:before {
    border-radius: 50%;
  }
}

