@media screen and (max-width: 780px) {
  .ms1_5 {
    text-decoration: none;
    top: 120px;
    /* left: px; */
    width: 100%;
    height: auto;
    color: #000122;
    padding-left: 30px;
    padding-right: 30px;
    text-align: left;
    padding-top: 40px;
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
    background: rgba(241.36, 241.7, 249.69, 1);
    bottom: 80px;
    position: fixed;
    left: 0;
  }

  .ms1_6 {
    text-decoration: none;
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
  }

  .ms1_7 {
    text-decoration: none;
    display: flex;
  }

  .ms1_7 img {
    width: 60px;
    height: 60px;
    border-radius: 30px;
  }

  .ms1_9 {
    margin-left: 10px;
  }

  .ms1_11 {
    font-size: 15px;
  }

  .ms1_12 {
    margin-left: 5px;
    width: 20px;
    height: 20px;
    border-radius: 10px;
    text-align: center;
    color: white;
    background-color: #ce78c5;
    padding: 0;
    margin-top: 10px;
  }

  .ms1_13 {
    margin-top: 15px;
  }

  .send {
    position: absolute;
    width: 50px;
    height: 50px;
    left: 85%;
    top: 90%;
    border-radius: 25px;
    text-align: center;
    align-items: center;
    cursor: pointer;
    background: #000aff;
    box-shadow: 0px 15px 40px rgba(0, 10, 255, 0.35);
  }
  .send img {
    margin-top: 10px;
    margin-right: 5px;
    width: 30px;
    height: 30px;
  }
}
