@media screen and (max-width: 781px) {
  * {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  body {
    margin: 0px;
    padding: 0px;
    font-family: "Arial";
    background: grey;
    text-align: center;
    justify-content: center;
  }

  .logIn {
    margin-top: 0%;

    width: 100%;
    height: 100vh;
    background: rgba(0, 1.36, 34, 1);
    color: white;
  }
  .logIn_heading {
    padding-top: 30px;
    padding-bottom: 60px;
    position: fixed;
    left: 35%;
    right: 35%;
  }
  .signIn {
    top: 120px;
    /* left: px; */
    width: 100%;
    height: auto;
    color: #000122;
    padding-left: 30px;
    padding-right: 30px;
    text-align: left;
    padding-top: 40px;
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
    background: rgba(241.36, 241.7, 249.69, 1);
    bottom: 80px;
    position: fixed;
    left: 0;
  }
  .signIn input {
    border-top: none;
    border-left: none;
    border-right: none;
    width: 100%;
    height: 40px;
    font-size: 20px;
    background-color: transparent;
    margin-bottom: 20px;
    margin-top: 20px;
    display: block;
  }
  .forget input {
    display: inline;
    background-color: #000122;
    width: 13px;
    height: 13px;
  }
  .forget a {
    margin-left: 50px;
    text-decoration: none;
  }
  .button {
    display: block;
    height: 50px;
    width: 100%;
    margin-top: 20px;
    border-radius: 20px;
    border: 1px solid rgba(47, 46, 46, 0.662);
    cursor: pointer;
    background-color: transparent;
    font-family: Roboto;
    font-size: 20px;
    text-align: center;
    line-height: 18px;
    color: black;
    font-weight: bold;
  }
  /* .button:hover{
        background-color: #000AFF;
    } */
  .logIn_footer {
    color: black;
    height: 80px;
    width: 100%;
    background: rgba(255, 255, 255, 1);
    bottom: 0;
    position: fixed;
    text-align: center;
    font-size: 20px;
    padding-top: 30px;
    font-weight: bold;
  }
  .active {
    background-color: #000aff;
    border: none;
    color: #ffffff;
  }
}
