* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: #344a72;
  font-family: "Roboto", sans-serif;
  text-align: center;
}

.signup-box {
  width: 100%;
  /* height: 90vh; */

  /* background-color: white; */
  border-radius: 3px;
}

.login-box {
  width: 100%;
  height: 100%;

  border-radius: 3px;
  background-color: white;
}

h1 {
  text-align: center;
  padding-top: 15px;
}

h4 {
  text-align: center;
}

form {
  padding: 10px;

  width: 100%;
}

form label {
  display: flex;
  margin-top: 10px;
  font-size: 18px;
}

form input {
  width: 100%;
  padding: 7px;
  border: none;
  background-color: transparent;
  border: 1px solid gray;
  border-radius: 6px;
  outline: none;
}
input[type="button"] {
  width: 100%;
  align-self: center;
  height: 35px;
  margin-top: 20px;
  border: none;
  background-color: #49c1a2;
  color: white;
  font-size: 18px;
}
p {
  text-align: center;
  padding-top: 20px;
  font-size: 14px;
}
.para-2 {
  text-align: center;
  color: black;
  font-size: 18px;
  margin-top: -10px;
}
.para-2 a {
  color: #49c1a2;
}
