@media screen and (max-width: 780px) {
  * {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }

  body {
    margin: 0px;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    justify-content: center;
  }

  .s1_1 {
    margin-top: 0%;

    width: 100%;
    height: 100vh;
    background: rgba(0, 1.36, 34, 1);
    color: white;
  }

  .s1_2 {
    padding-top: 51px;
    margin-left: 30px;
    margin-right: 30px;
    display: flex;
    justify-content: space-between;
  }
  .s1_2 {
    margin-top: -5px;
  }
  .s1_3 {
    display: flex;
    margin-top: 20px;
  }

  .s1_3 img {
    color: #ce78c5;
    width: 25px;
    height: 25px;
  }

  .s1_5 {
    text-decoration: none;
    top: 120px;
    /* left: px; */
    width: 100%;
    height: auto;
    color: #000122;
    padding-left: 30px;
    padding-right: 30px;
    text-align: left;
    padding-top: 40px;
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
    background: rgba(241.36, 241.7, 249.69, 1);
    bottom: 80px;
    position: fixed;
    left: 0;
  }
  .s1_6 {
    margin-top: 30px;
    font-size: 20px;
    text-decoration: none;
    font-weight: 400;
    font-family: Arial, Helvetica, sans-serif;
    margin-bottom: 10px;
  }
  .s1_7 {
    color: blue;
    margin-top: 10px;
    margin-bottom: 10px;
    text-decoration: none;
  }
}
