    font-size: 30px;
    text-decoration: none;
  }
  .m1_6 {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    margin-bottom: 20px;
    text-decoration: none;
  }
  .m1_7 {
    display: flex;
    flex-direction: row;
    text-decoration: none;
