body {
  background: #f2f2f2;
}

.payment {
  margin-top: 100px;
  border: 1px solid #f2f2f2;
  height: 380px;
  border-radius: 20px;
  background: #fff;
}
.payment_header {
  background: rgba(255, 102, 0, 1);
  padding: 20px;
  border-radius: 20px 20px 0px 0px;
}

.check {
  margin: 0px auto;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background: #fff;
  text-align: center;
}

.check i {
  vertical-align: middle;
  line-height: 50px;
  font-size: 30px;
}

.content {
  text-align: center;
  margin-top: 10px;
}

.content h1 {
  font-size: 25px;
  padding-top: 25px;
}
.content p {
  padding: 10px;
  margin-bottom: 20px;
}

.content a {
  color: #fff;
  border-radius: 30px;
  padding: 5px 10px;
  background: rgba(255, 102, 0, 1);
  transition: all ease-in-out 0.3s;
  margin-top: 50px;
}

.content a:hover {
  text-decoration: none;
  background: #000;
}
