/* Import Google Font - Poppins */
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
@media screen and (max-width: 780px) {
  ::selection {
    color: #fff;
    background: #675afe;
  }
  .s1_5 {
    text-decoration: none;
    top: 120px;
    /* left: px; */
    width: 100%;
    height: auto;
    color: #000122;
    padding-left: 30px;
    padding-right: 30px;
    text-align: left;
    padding-top: 40px;
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
    background: rgba(241.36, 241.7, 249.69, 1);
    bottom: 80px;
    position: fixed;
    left: 0;
  }
  .wrapper11 {
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
    width: 100%;
    /* padding: 30px; */
    /* border-top-left-radius: 20px; */
    background-color: transparent;
    /* border-radius: 7px; */
    background: rgba(241.36, 241.7, 249.69, 1);
    box-shadow: 7px 7px 20px rgba(0, 0, 0, 0.05);
  }
  .wrapper header {
    font-size: 28px;
    font-weight: 500;
    text-align: center;
  }
  .wrapper form {
    margin: 40px 0 20px 0;
  }
  form :where(input, select, button) {
    width: 100%;
    outline: none;
    border-radius: 5px;
    border: none;
  }
  form p {
    font-size: 18px;
    margin-bottom: 5px;
  }
  form input {
    height: 50px;
    font-size: 17px;
    padding: 0 15px;
    border: 1px solid #999;
  }
  form input:focus {
    padding: 0 14px;
    border: 2px solid #675afe;
  }
  form .drop-list {
    display: flex;
    margin-top: 20px;
    align-items: center;
    justify-content: space-between;
  }
  .drop-list .select-box {
    display: flex;
    width: 115px;
    height: 45px;
    align-items: center;
    border-radius: 5px;
    justify-content: center;
    border: 1px solid #999;
  }
  .select-box img {
    max-width: 21px;
  }
  .select-box select {
    width: auto;
    font-size: 16px;
    background: none;
    margin: 0 -5px 0 5px;
  }
  .select-box select::-webkit-scrollbar {
    width: 8px;
  }
  .select-box select::-webkit-scrollbar-track {
    background: #fff;
  }
  .select-box select::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 8px;
    border-right: 2px solid #ffffff;
  }
  .drop-list .icon {
    cursor: pointer;
    margin-top: 30px;
    font-size: 22px;
  }
  form .exchange-rate {
    font-size: 17px;
    margin: 20px 0 30px;
  }
  form button {
    height: 52px;
    color: #fff;
    font-size: 17px;
    cursor: pointer;
    background: #675afe;
    transition: 0.3s ease;
  }
  form button:hover {
    background: #4534fe;
  }
}
