@import url("https://fonts.googleapis.com/css2?family=Lato&family=Poppins&display=swap");

.wrapper1 {
  background: rgba(241.36, 241.7, 249.69, 1);
  color: black;
  padding: 10px 20px;
}
.dark,
.input:focus {
  color: black;
  background: rgba(241.36, 241.7, 249.69, 1);
}
.navbar {
  color: black;
  padding: 0.5rem 0rem;
}
.navbar .navbar-brand {
  color: black;
  font-size: 30px;
}
#income {
  color: black;
  border-right: 1px solid rgb(151, 143, 143);
}
.notify {
  color: black;
  display: none;
}
.nav-item .nav-link .fa-bell-o,
.fa-long-arrow-down,
.fa-long-arrow-up {
  padding: 10px 10px;
  color: black;
  background: rgba(241.36, 241.7, 249.69, 1);
  border-radius: 50%;
  position: relative;
  font-size: 18px;
}
.nav-item .nav-link .fa-bell-o::after {
  content: "";
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: #ffc0cb;
  right: 10px;
  top: 10px;
  color: black;
}
.nav-item input {
  border: none;
  outline: none;
  box-shadow: none;
  padding: 3px 8px;
  width: 75%;
  color: black;
}
.nav-item .fa-search {
  font-size: 18px;
  color: black;
  cursor: pointer;
}
.navbar-nav:last-child {
  display: flex;
  align-items: center;
  width: 40%;
  color: black;
}
.navbar-nav .nav-item {
  padding: 0px 0px 0px 10px;
  color: black;
}
.navbar-brand p {
  display: block;
  font-size: 14px;
  margin-bottom: 3px;
  color: black;
}
.text {
  color: black;
}
.money {
  font-family: "Lato", sans-serif;
  color: black;
}
.fa-long-arrow-down,
.fa-long-arrow-up {
  padding-left: 12px;
  padding-top: 8px;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  font-size: 1rem;
  font-weight: 100;
  color: #28df99;
}
.fa-long-arrow-up {
  color: #ffa500;
}
.nav.nav-tabs {
  border: none;
  color: black;
}
.nav.nav-tabs .nav-item .nav-link {
  color: black;
  border: none;
}
.nav.nav-tabs .nav-link.active {
  background: rgba(241.36, 241.7, 249.69, 1);
  border: none;
  color: black;
  border-bottom: 4px solid #fff;
}
.nav.nav-tabs .nav-item .nav-link:hover {
  border: none;
  color: black;
}
.nav.nav-tabs .nav-item .nav-link.active:hover {
  border-bottom: 4px solid #fff;
  color: black;
}
.nav.nav-tabs .nav-item .nav-link:focus {
  border-bottom: 4px solid #fff;
  color: black;
}
.table-dark {
  background: rgba(241.36, 241.7, 249.69, 1);
  color: black;
}
.table thead th {
  text-transform: uppercase;
  color: black;
  font-size: 14px;
}
.table thead th,
.table td,
.table th {
  border: none;
  overflow: auto auto;
  color: black;
}
td .fa-briefcase,
td .fa-bed,
td .fa-exchange,
td .fa-cutlery {
  color: #ff6347;
  background: rgba(241.36, 241.7, 249.69, 1);
  padding: 5px;
  border-radius: 50%;
}
td .fa-bed,
td .fa-cutlery {
  color: black;
}
td .fa-exchange {
  color: black;
}
tbody tr td .fa-cc-mastercard,
tbody tr td .fa-cc-visa {
  color: black;
}
tbody tr td.text-muted {
  font-family: "Lato", sans-serif;
  color: black;
}
tbody tr td .fa-long-arrow-up,
tbody tr td .fa-long-arrow-down {
  font-size: 12px;
  padding-left: 7px;
  padding-top: 3px;
  height: 20px;
  width: 20px;
  color: black;
}
.results span {
  color: black;
  font-size: 12px;
}
.results span b {
  font-family: "Lato", sans-serif;
  color: black;
}
.pagination .page-item .page-link {
  background: rgba(241.36, 241.7, 249.69, 1);
  color: black;
  padding: 0.3rem 0.75rem;
  border: none;
  border-radius: 0;
}
.pagination .page-item .page-link span {
  font-size: 16px;
  color: black;
}
.pagination .page-item.disabled .page-link {
  background: rgba(241.36, 241.7, 249.69, 1);
  color: black;
  cursor: crosshair;
}
@media (min-width: 768px) and (max-width: 991px) {
  .wrapper {
    margin: auto;
    color: black;
  }
  .navbar-nav:last-child {
    display: flex;
    align-items: start;
    justify-content: center;
    width: 100%;
    color: black;
  }
  .notify {
    display: inline;
    color: black;
  }
  .nav-item .fa-search {
    padding-left: 10px;
    color: black;
  }
  .navbar-nav .nav-item {
    padding: 0px;
    color: black;
  }
}
@media (min-width: 300px) and (max-width: 767px) {
  .wrapper {
    margin: auto;
    color: black;
  }
  .navbar-nav:last-child {
    display: flex;
    align-items: start;
    justify-content: center;
    width: 100%;
    color: black;
  }
  .notify {
    display: inline;
    color: black;
  }
  .nav-item .fa-search {
    padding-left: 10px;
    color: black;
  }
  .navbar-nav .nav-item {
    padding: 0px;
    color: black;
  }
  #income {
    border: none;
    color: black;
  }
}
@media (max-width: 400px) {
  .wrapper {
    padding: 10px 15px;
    margin: auto;
    color: black;
  }
  .btn {
    font-size: 12px;
    padding: 10px;
    color: black;
  }
  .nav.nav-tabs .nav-link {
    padding: 10px;
    color: black;
  }
}
